import React, { Component } from "react";

const Login = () => {
  const [username, setUsername] = React.useState("");

  const submitForm = (e) => {
      e.preventDefault();
      setUsername("Unkontrolliert!");
  };

  return ( 
    <form method = "post" onSubmit = { submitForm } >
      <p> Der Benutzername lautet: { username } </p>
      <p>< input type = "text"
           onChange = {
             (e) => setUsername(e.target.value) }
         />
         <input type = "submit" />
      </p>
    </form>
  );
};


export default Login;
